ul {
  margin: 0;
  list-style: none;
  padding: 0; }

@font-face {
  font-family: "Josefin Sans";
  src: url("../fonts/JosefinSans-Regular.ttf") format("truetype");
  font-weight: 400;
  font-style: normal; }

@font-face {
  font-family: "Josefin Sans";
  src: url("../fonts/JosefinSans-Bold.ttf") format("truetype");
  font-weight: 700;
  font-style: normal; }

* {
  box-sizing: border-box; }

body {
  font-family: "Josefin Sans", sans-serif;
  font-size: 18px;
  color: #484b6a; }

.page {
  display: flex;
  flex-direction: column;
  overflow: hidden;
  min-height: 100vh;
  background-color: black; }

main {
  display: flex;
  flex-direction: column;
  align-items: center; }

header {
  background: url("../../images/bg-mobile-dark.jpg") no-repeat #fafafa;
  display: flex;
  flex-direction: column;
  align-items: center; }

.header {
  width: 95%;
  padding: 45px 23px 37px 26px;
  display: flex;
  justify-content: space-between; }

h1 {
  text-transform: uppercase;
  letter-spacing: 0.3em;
  color: #fafafa;
  font-weight: 700;
  margin: 0;
  padding: 0;
  font-size: 1.8em; }

.header_switch {
  background: url("../../images/icon-sun.svg") no-repeat transparent center;
  border: none;
  outline: none;
  width: 40px;
  height: 30px; }

form {
  width: 80%;
  position: relative; }

input {
  width: 100%;
  height: 50px;
  background-color: #25273c;
  color: #484b6a;
  border-radius: 5px;
  border: none;
  outline: none;
  margin-bottom: 30px;
  font-size: 0.7em;
  padding-left: 50px; }

input::placeholder {
  color: #484b6a; }

.circle {
  height: 20px;
  width: 20px;
  position: absolute;
  border-radius: 50%;
  border: 1px solid #484b6a;
  top: 15px;
  left: 20px; }

@media (min-width: 373px) {
  header {
    background: url("../../images/bg-desktop-dark.jpg") no-repeat #fafafa; }
  form {
    width: 50%; } }

.todo {
  width: 80%;
  display: flex;
  flex-direction: column;
  font-size: 0.8em; }

.todo_total_items {
  float: left;
  font-size: 0.7em;
  background-color: #25273c;
  border-top-left-radius: 5px;
  border-bottom-left-radius: 5px;
  width: 50%;
  margin: -10px 0 15px 0;
  padding: 15px; }

.todo_total_completed {
  float: right;
  font-size: 0.7em;
  width: 50%;
  margin: -10px 0 15px 0;
  padding: 15px;
  background-color: #25273c;
  border-top-right-radius: 5px;
  border-bottom-right-radius: 5px;
  color: #484b6a;
  text-align: right; }

.todo_sort {
  display: flex;
  justify-content: space-around;
  background-color: #25273c;
  margin-bottom: 35px;
  padding: 20px 50px 20px 50px;
  border-radius: 5px; }

.todo_sort_item {
  padding-left: 5px; }

.todo_sort_item-active {
  color: #3a7bfd; }

.todo_paragraph {
  text-align: center; }

@media (min-width: 373px) {
  .todo {
    margin-top: -10px;
    width: 50%; }
  .todo_total {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    background-color: #25273c;
    width: 100%;
    border-radius: 5px;
    margin-bottom: 50px;
    padding: 15px; }
  .todo_total_items {
    font-size: 0.7em;
    order: 2;
    margin: 0;
    padding: 0; }
  .todo_total_completed {
    font-size: 0.7em;
    order: 4;
    margin: 0;
    padding: 0; }
  .todo_sort {
    font-size: 0.8em;
    font-weight: bold;
    order: 3;
    margin: 0;
    padding: 0; }
  .todo_sort_item {
    padding-left: 10px; } }

footer {
  font-size: 11px;
  text-align: center;
  margin-top: auto;
  margin-bottom: 20px; }

footer a {
  color: #3e52a3; }

.light .page {
  background-color: #e4e5f1; }

.light header {
  background: url("../../images/bg-mobile-light.jpg") no-repeat #fafafa; }

.light .header_switch {
  background: url("../../images/icon-moon.svg") no-repeat transparent center; }

.light .todo_sort {
  background-color: #fafafa; }

.light input {
  background-color: #fafafa;
  color: #fafafa; }

.light .todo_total_items {
  background-color: #fafafa; }

.light .todo_total_completed {
  background-color: #fafafa; }

@media (min-width: 373px) {
  .light header {
    background: url("../../images/bg-desktop-light.jpg") no-repeat #fafafa; }
  .light .todo_total {
    background-color: #fafafa; }
  .light input {
    color: #484b6a; } }
